import {Component, ElementRef, Input} from '@angular/core';
import {ISelectedOption} from '../interfaces/selected-option.interface';

@Component({
  selector: 'mdb-option',
  template: "<div class=\"completer-row\" (mousedown)=\"handleMouseDown($event)\" mdbAutoCompleterOption> <ng-content></ng-content> </div> "

})

export class MdbOptionComponent {
  @Input() value: string;
  clicked = false;
  selectedItem: ISelectedOption;

  constructor(public el: ElementRef) {
    this.clicked = false;
  }

  handleMouseDown(event: any) {
    const text = this.value || event.target.text || event.target.textContent || event.target.value;
    this.selectedItem = {text: text.trim(), element: this};
    this.clicked = true;
  }

}
